.statistics-popup-container {
    position: relative;
    display: inline-block;
}

.statistics-popup-inner {
    position: absolute;
    right: 0%;
    bottom: 30px;
    background: #fff;
    box-shadow: 0px 0px 5px #666;
    border-bottom: 3px solid orange;
    border-top: 3px solid #666;
    padding: 0px 20px;
    font-size: 0.8em;
    padding: 5px;
}

.statistics-link,
.statistics-link:hover {
    text-decoration: none;
    color: #333;
    font-size: 0.8em;
}

.statistics-popup-inner table td,
.statistics-popup-inner table th {
    white-space: nowrap;
    padding: 5px 10px;
    border: none;
}

.statistics-popup-inner .lowest-text td:nth-child(1){
    color: green;
}

.statistics-popup-inner .current-text td:nth-child(1){
    color: black;
}

.statistics-popup-inner .highest-text td:nth-child(1){
    color: red;
}

.statistics-popup-inner .average-text td:nth-child(1){
    color: black;
}

.statistics-popup-inner .drops-text td:nth-child(1){
    color: black;
}